
<!-- 用户评价数据渲染 -->

<template>
    <el-table :data="tableData" style="width: 100%">

        <el-table-column label="用户" align="center">
            <template slot-scope="scope">
                <div class="user">
                    <img src="@/assets/img/cheongsam/10005.jpg" width="50" height="50" style="border-radius:50%;">
                    {{ scope.row.user }}
                </div>
            </template>
        </el-table-column>
        <el-table-column prop="appraise" label="评价内容" width="260" align="center">
        </el-table-column>
        <el-table-column prop="star" label="评价星级" align="center">
        </el-table-column>
        <el-table-column prop="appdate" label="评价时间" align="center">
        </el-table-column>
        <el-table-column label="操作" align="center">
            <template slot-scope="scope">
                <el-button type="text">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
</template>
<script>
import { appraise } from '@/request/api'
export default {
    beforeCreate() {
        appraise().then(res => this.tableData = res)
    },
    data() {
        return {
            tableData: null,
        }
    }
}
</script>
<style lang="scss" scoped>
.user {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
        margin-right: 10px;
    }
}
</style>